
  import { defineComponent, ref, onMounted } from "vue";

  export default defineComponent({
    emits: [
      "onAddItem",
      "onDeleteChecked",
      "onResetChecked",
      "onClearList",
      "onSetList",
    ],
    props: ["lists"],

    setup(props, { emit }) {
      const input = ref();

      // TODO: Add proper type
      function emitOnSetList(list: Event) {
        emit("onSetList", list);
      }
      function resetInput() {
        input.value.value = "";
        input.value.focus();
      }

      function emitOnAddItem() {
        emit("onAddItem", input.value.value);
        resetInput();
      }
      function emitOnDeleteChecked() {
        emit("onDeleteChecked");
      }
      function emitOnResetChecked() {
        emit("onResetChecked");
      }
      function emitOnClearList() {
        emit("onClearList");
      }
      return {
        input,
        emitOnSetList,
        resetInput,
        emitOnAddItem,
        emitOnDeleteChecked,
        emitOnResetChecked,
        emitOnClearList,
      };
    },
  });
